import ajax from './index'

export const getPcrSetting = () => {return ajax({url:'/setting/pcr.json',method:'get'})}
export const getPcrPools = () => {return ajax({url:'/setting/pcr/pools.json',method:'get'})}
export const getPcrCharacter = () => {return ajax({url:'/setting/pcr/character.json',method:'get'})}

export const savePcrSetting = (params) => {return ajax({url:'/setting/pcr-setting.save',method:'post',data:params})}
export const saveAllPcrSetting = (params) => {return ajax({url:'/setting/pcr-all.save',method:'post',data:params})}
export const savePcrPoolsSetting = (params) => {return ajax({url:'/setting/pcr-pools.save',method:'post',data:params})}



